import React from "react";
import { Container, Row, Col, Image, Button } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import Poland from "../img/Poland.png";
import { connect } from "react-redux";
import "./HomePage.scss";

const HomePage = (props) => {
  return (
    <Container className="bg-light border rounded shadow-container create-market-container">
      <header>
        <h2>Docz do PredictBom ju dzi !</h2>
        <hr className="my-4"></hr>
      </header>
      <Row>
        <Col sm={6}>
          <Image variant="top" src={Poland} className="img-cover" />
        </Col>
        <Col sm={6}>
          <div className="advantages home-info">
            <h4>Dlaczego warto do nas doczy ?</h4>
            <p className="text-muted">
              <strong>
                PredictBom jest darmow aplikacj zwizan z rynkami
                prognostycznymi.
              </strong>
              We udzia w rynkach dotyczcych najciekawszych wydarze
              dziejcych si w naszym kraju z zakresu kilku kategorii.
            </p>
          </div>
          <div className="rules home-info">
            <h4>Zasady gry</h4>
            <p className="text-muted">
              Gracze PredictBom otrzymuj moliwo obrotu akcjami na wielu
              interesujcych rynkach prognoz. Ceny akcji wahaj si midzy 0 a 1
              $, gdzie za kad akcj na poprawn opcj zakadow otrzymuj 1 $.
              Wyka si umiejtnosciami prognostycznymi i kupuj akcje po jak
              najbardziej korzystnych cenach i sprzedawaj je aby jak najbardziej
              zwikszy swj budet. Zakupione akcje znajdziesz w zakadce
              kontrakty. Aby gra bya uczciwa i najbogatsi gracze nie wykupowali
              wszystkich akcji dla danego rynku,{" "}
              <strong>
                dziennie moliwy jest zakup 1000 akcji na wybran opcj
                zakadow.
              </strong>
            </p>
          </div>
          <div className="bonus home-info">
            <h4>Odwiedzaj nas codziennie aby odebra bonus !</h4>
            <p className="text-muted">
              Nie zawsze bdziesz dobrze obstawia, aby odzyska budet
              odwiedzaj nas codziennie!{" "}
              <strong>
                Przy pierwszym logowaniu kadego dnia otrzymasz 100$.
              </strong>
            </p>
          </div>
          {!props.loggedIn && (
            <div className="text-center">
              <LinkContainer to="/register" className="link-button">
                <Button variant="dark">Docz do PredictBom</Button>
              </LinkContainer>
            </div>
          )}
          <div className="text-center">
            <LinkContainer to="/markets" className="link-button">
              <Button variant="dark">Przegldaj rynki</Button>
            </LinkContainer>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    loggedIn: state.login.loggedIn,
  };
};

export default connect(mapStateToProps)(HomePage);
